html {
    font-size: 62.5%;
}

body {
    margin: 0;
    padding: 29px00;
    font: 12px"\5B8B\4F53", sans-serif;
    background: #CCCCCC;
    user-select: none;
    font-family: "微软雅黑";
}

div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,blockquote,p {
    padding: 0;
    margin: 0;
}

table,td,tr,th {
    font-size: 12px;
}

li {
    list-style-type: none;
}

img {
    vertical-align: top;
    border: 0;
}

ol,ul {
    list-style: none;
}

h1,h2,h3,h4,h5,h6 {
    font-size: 12px;
    font-weight: normal;
}

address,cite,code,em,th {
    font-weight: normal;
    font-style: normal;
}


